<template>
  <router-view></router-view>
</template>

<script>
import { mapActions, mapState } from 'vuex'

export default {
  computed: {
    ...mapState({
      signature: state => state.commonMain.signature
    })
  },
  methods: {
    ...mapActions(['fetchWechatSignature', 'showToast'])
  },
  created: async function () {
    const options = { params: { url: window.location.href } }
    await this.fetchWechatSignature(options)

    const signature = this.signature
    const wx = window.wx
    if (wx) {
      console.log(wx)
      wx.config({
        debug: true, // ,apialertpclogpc
        appId: signature.appid, // 
        timestamp: parseInt(signature.timestamp, 10), // 
        nonceStr: signature.noncestr, // 
        signature: signature.signature, // 1
        jsApiList: [
          'onMenuShareTimeline', // 
          'onMenuShareAppMessage', // 
          'onMenuShareQQ', // QQ
          'onMenuShareWeibo', // ] // JSJS2
          'hideAllNonBaseMenuItem',
          'chooseImage'
        ]
      })

      wx.ready(function () {
        wx.hideAllNonBaseMenuItem();

        wx.chooseImage({
          count: 1, // 9
          sizeType: ['original', 'compressed'], // 
          sourceType: ['album', 'camera'], // 
          success: function (res) {
            var localIds = res.localIds // IDlocalIdimgsrc
          }
        })
      })
    }
  }
}
</script>



